	import ButtonItems from '../../components/ButtonItems.vue'
	import ListItem from '../../components/ListItem.vue'
	import topBar from '../../components/TopBar.vue'
	export default {
	data() {
        return {
            scrollLeft: 0,
			isClickChange: false,
			tabIndex: 0,
			newsitems: [],
			tabBars: [{
				name: '金融智投',
				id: 'guanzhu'
			}, {
				name: '新标专区',
				id: 'tuijian'
			}, {
				name: '转让专区',
